body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.Top-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 1em;
  padding-right: 3em;
}

.Top-bar.nurse {
  background: #3CAEA3
}

.Top-bar.doctor {
  background: #20639B;
  color: white;
}

.PatientDetails {
  width: 40em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.sidebar.mdc-list {
  width: 15em;
  padding: 0;
}

.mdc-ripple-upgraded.mdc-list-item {
  color: grey;
}

.mdc-ripple-upgraded.mdc-list-item.mdc-list-item--activated {
  color: black;
  background: #F7E88F
}

.Body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100vw;
}

.Content {
  width: 100vw;
}

.NurseContent {
  padding-left: 2em;
  margin: 3em;
}

.mdc-typography--headline4 {
  text-align: left;
}

.Measurements {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 30em;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.Doctor {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.BloodPressure {
  padding: 1em 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.Break {
  padding: 1em;
}

.AlertDialog {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column
}
